const useFetch = (url) => {
  const [dementiaClinics, setDementiaClinics] = useState(null);
  useEffect(() => {
    (async () => {
      const response = await fetch(url);
      const data = await response.json();
      setDementiaClinics(data);
    })();
  }, [url]);
  return dementiaClinics;
};

