/**
 * The website navigaiton bar part
 *
 * @author (Yechao Mao)
 * @version (26/10/2020)
 */
import React, { useContext, useState, useEffect } from "react";
import { Link } from "react-router-dom";
import image from "../../static/images/logo_2.png";
import "./styles.less";
import { Nav, Navbar, Button, Modal, NavDropdown } from "react-bootstrap";
import { LangContext, ActiveContext } from "../../App";
import axios from "axios";
import classnames from "classnames";

function NavigationBar() {
  const [show, setShow] = useState(false);
  const { lang } = useContext(LangContext);
  const [convertedText, setConvertedText] = useState("");
  let text =
    "Home|Understand Dementia|Home Tests|Find Location|Keep Safe|Language|" +
    "Consent|I acknowledge that these tests are just a tool for users to see if they have similar symptoms of dementia." +
    " The results are not professional and for clinical use.|For the tests below, it requires a family member or a friend as a" +
    " tester to assist the test target in finishing the tests.|Close|Accept|";

  let items;
  const { color, colorDispatch } = useContext(ActiveContext);
  useEffect(() => {
    axios
      .post(
        "https://translation.googleapis.com/language/translate/v2",
        {},
        {
          params: {
            q: text,
            target: lang,
            key: "AIzaSyBdDbvl9MB85OUVGdKb4-IN_ayxScHalEs",
          },
        }
      )
      .then((response) => {
        setConvertedText(response.data.data.translations[0].translatedText);
      })
      .catch((err) => {
        console.log("rest api error", err);
      });
  }, [text, lang]);
  items = convertedText
    .split("|")
    .map((data) => (data = data.replace("&#39;", "'")));
  return (
    <div className="fixed-top">
      <div className="navigationbar">
        <div className="header">
          <Navbar expand="lg" style={{ padding: "0px" }}>
            <Navbar.Brand href="/#home" style={{ padding: "0px" }}>
              <img
                src={image}
                height="72px"
                alt="Cald Dementia"
                className="d-inline-block align-top"
              />
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="mr-auto"></Nav>
              <Nav style={{ marginTop: "16px" }}>
                <Nav.Link
                  href="/#home"
                  style={{ padding: "0 8px" }}
                  onClick={() => colorDispatch({ type: "zero" })}
                >
                  <p
                    className={classnames(
                      { notactive: !color[0] },
                      { active: color[0] }
                    )}
                  >
                    Home
                  </p>
                </Nav.Link>
                <Nav.Link
                  href="/#information"
                  style={{ padding: "0 8px" }}
                  onClick={() => colorDispatch({ type: "one" })}
                >
                  <p
                    className={classnames(
                      { notactive: !color[1] },
                      { active: color[1] }
                    )}
                  >
                    Understand Dementia
                  </p>
                </Nav.Link>
                <Nav.Link
                  style={{ padding: "0 8px" }}
                  onClick={() => {
                    setShow(true);
                  }}
                >
                  <p
                    className={classnames(
                      { notactive: !color[2] },
                      { active: color[2] }
                    )}
                  >
                    Home Tests
                  </p>
                </Nav.Link>
                <Nav.Link
                  href="/#map"
                  style={{ padding: "0 8px" }}
                  onClick={() => colorDispatch({ type: "three" })}
                >
                  <p
                    className={classnames(
                      { notactive: !color[3] },
                      { active: color[3] }
                    )}
                  >
                    Find Location
                  </p>
                </Nav.Link>
                <Nav.Link
                  href="/#check"
                  style={{ padding: "0 8px" }}
                  onClick={() => colorDispatch({ type: "four" })}
                >
                  <p
                    className={classnames(
                      { notactive: !color[4] },
                      { active: color[4] }
                    )}
                  >
                    Keep Safe
                  </p>
                </Nav.Link>
                <Nav.Link
                  href="/"
                  style={{ padding: "0 8px" }}
                  onClick={() => colorDispatch({ type: "five" })}
                >
                  <p
                    className={classnames(
                      { notactive: !color[5] },
                      { active: color[5] }
                    )}
                  >
                    Language
                  </p>
                </Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </div>
      </div>
      <Modal show={show} onHide={() => setShow(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Consent</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            I acknowledge that these tests are just a tool for users to see if
            they have similar symptoms of dementia. The results are not
            professional and for clinical use.
          </p>
          <p>
            For the tests below, it requires a family member or a friend as a
            tester to assist the test target in finishing the tests.
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShow(false)}>
            Close
          </Button>
          <Link to="/test">
            <Button
              variant="success"
              onClick={() => {
                setShow(false);
                colorDispatch({ type: "two" });
              }}
            >
              Accept
            </Button>
          </Link>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default NavigationBar;
