import React, { useState, useReducer } from "react";
import "./index.less";
import AboutDementia from "./AboutDementia";
import Diagnosing from "./Spotting";
import Family from "./FamilyAndFriends";
import Early from "./EarlyPlanning";
import Culture from "./Cultural";
import Statistic from "./Statistic";
import BehaviourAndMemory from "./BehavioursChange";
import Convert from "../../components/Translate/Translate";
import { Container, Row, Col, ListGroup } from "react-bootstrap";
import classnames from "classnames";
export const LeftNavContext = React.createContext("");
export const InfoTitleContext = React.createContext("");

const initialActive = [true, false, false, false, false, false, false];
function leftNavReducer(active, action) {
  switch (action.type) {
    case "one":
      return [true, false, false, false, false, false, false];
    case "two":
      return [false, true, false, false, false, false, false];
    case "three":
      return [false, false, true, false, false, false, false];
    case "four":
      return [false, false, false, true, false, false, false];
    case "five":
      return [false, false, false, false, true, false, false];
    case "six":
      return [false, false, false, false, false, true, false];
    case "seven":
      return [false, false, false, false, false, false, true];
    default:
      return [true, false, false, false, false, false, false];
  }
}

function InformationHome() {
  let lang = localStorage.getItem("language");
  const [subtitle, setSubtitle] = useState("About Dementia");
  let text =
    "About Dementia|Spotting the Signs of Dementia|Information for Family and Friend|Early Planning|Behaviours and Memory Changes|Cultural Difference|Navigation|Information|Statistic|" +
    subtitle;
  let items, isloading;
  if (lang !== "en") {
    let { convertedText, loading } = Convert(text, lang);
    isloading = loading;
    items = convertedText
      .split("|")
      .map((data) => (data = data.replace("&#39;", "'")));
  } else {
    isloading = false;
    items = text.split("|");
  }
  const [selections, setSelection] = useState(<AboutDementia />);
  const [leftAvtive, activeDispatch] = useReducer(
    leftNavReducer,
    initialActive
  );
  return (
    <main id="information">
      <Container fluid="true">
        <Row>
          <Col sm={3}>
            {isloading ? (
              <div>...loading</div>
            ) : (
              <div id="left-part">
                <p>
                  <span>
                    {items[7]} {">"}{" "}
                  </span>
                  {items[9]}
                </p>
                <ListGroup>
                  <ListGroup.Item id="left-nav-title" action>
                    <h5>{items[6]}</h5>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    onClick={() => {
                      setSubtitle("About Dementia");
                      activeDispatch({ type: "one" });
                      setSelection(<AboutDementia />);
                      window.scrollTo(0, 0);
                    }}
                    className={classnames(
                      { notactive: !leftAvtive[0] },
                      { active: leftAvtive[0] }
                    )}
                  >
                    <p className="left-side-nav-link">{items[0]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    onClick={() => {
                      setSubtitle("Spotting the Signs of Dementia");
                      activeDispatch({ type: "two" });
                      setSelection(<Diagnosing />);
                      window.scrollTo(0, 0);
                    }}
                    className={classnames(
                      { notactive: !leftAvtive[1] },
                      { active: leftAvtive[1] }
                    )}
                  >
                    <p className="left-side-nav-link">{items[1]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    className={classnames(
                      { notactive: !leftAvtive[2] },
                      { active: leftAvtive[2] }
                    )}
                    onClick={() => {
                      setSubtitle("Information for Family and Friend");
                      setSelection(<Family />);
                      activeDispatch({ type: "three" });
                      window.scrollTo(0, 0);
                    }}
                  >
                    <p className="left-side-nav-link">{items[2]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    className={classnames(
                      { notactive: !leftAvtive[3] },
                      { active: leftAvtive[3] }
                    )}
                    onClick={() => {
                      setSubtitle("Early Planning");
                      setSelection(<Early />);
                      activeDispatch({ type: "four" });
                      window.scrollTo(0, 0);
                    }}
                  >
                    <p className="left-side-nav-link">{items[3]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    className={classnames(
                      { notactive: !leftAvtive[4] },
                      { active: leftAvtive[4] }
                    )}
                    onClick={() => {
                      setSubtitle("Behaviours and Memory Changes");
                      setSelection(<BehaviourAndMemory />);
                      activeDispatch({ type: "five" });
                      window.scrollTo(0, 0);
                    }}
                  >
                    <p className="left-side-nav-link">{items[4]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    className={classnames(
                      { notactive: !leftAvtive[5] },
                      { active: leftAvtive[5] }
                    )}
                    onClick={() => {
                      setSubtitle("Cultural Difference");
                      setSelection(<Culture />);
                      activeDispatch({ type: "six" });
                      window.scrollTo(0, 0);
                    }}
                  >
                    <p className="left-side-nav-link">{items[5]}</p>
                  </ListGroup.Item>
                  <ListGroup.Item
                    action
                    className={classnames(
                      { notactive: !leftAvtive[6] },
                      { active: leftAvtive[6] }
                    )}
                    onClick={() => {
                      setSubtitle("Statistic");
                      setSelection(<Statistic />);
                      activeDispatch({ type: "seven" });
                      window.scrollTo(0, 0);
                    }}
                  >
                    <p className="left-side-nav-link">{items[8]}</p>
                  </ListGroup.Item>
                </ListGroup>
              </div>
            )}
          </Col>
          <Col sm={8}>
            <div>{selections}</div>
          </Col>
        </Row>
      </Container>
    </main>
  );
}

export default InformationHome;
