import React, { useContext } from "react";
import "./index.less";
import { Link } from "react-router-dom";
import { Button, Container, Row, Col } from "react-bootstrap";
import Convert from "../../../components/Translate/Translate";
import { ActiveContext } from "../../../App";
import first from "../../../static/images/123.jpg";
import second from "../../../static/images/234.jpg";
function Barthel() {
  let lang = localStorage.getItem("language");
  let text =
    "Tests and Safety|Indoor Safety|Barthel Index|Understand Dementia|Find Location|If you want to know more about dementia, please go:|If you want to find some dementia service locations, please go:|Return to Keep Safe| Go to Home Safety";
  let items;
  if (lang !== "en") {
    let { convertedText } = Convert(text, lang);
    items = convertedText
      .split("|")
      .map((data) => (data = data.replace("&#39;", "'")));
  } else {
    items = text.split("|");
  }
  let quiz;
  switch (lang) {
    case "zh-CN":
      quiz = 738570;
      break;
    case "it":
      quiz = 738748;
      break;
    case "vi":
      quiz = 738757;
      break;
    case "ar":
      quiz = 738760;
      break;
    default:
      quiz = 737126;
  }
  const { colorDispatch } = useContext(ActiveContext);
  return (
    <main className="barthel">
      <p className="intro">
        <span>
          {items[0]} {">"}{" "}
        </span>
        <span>
          {items[1]} {">"}{" "}
        </span>
        {items[2]}
      </p>
      <Container style={{ maxWidth: "1960px" }}>
        <Row style={{ marginTop: "80px" }}>
          <Col lg={9} xs={12}>
            <div
              className="embed-responsive embed-responsive-16by9"
              //style={{ width: "99% !important" }}
            >
              <iframe
                className="embed-responsive-item"
                title="barthel"
                name="opinionstage-widget"
                src={
                  "https://www.opinionstage.com/api/v1/widgets/" +
                  quiz +
                  "/iframe?em=1"
                }
                frameBorder="0"
                allow="fullscreen"
                webkitallowfullscreen="true"
                mozallowfullscreen="true"
              ></iframe>
            </div>
            <div className="testbotton">
              <Link to="/check" style={{ marginTop: "20px" }}>
                <Button type="button" className="btn btn-info">
                  {items[7]}
                </Button>
              </Link>
              <Link to="/check/homesafety" style={{ marginTop: "20px" }}>
                <Button type="button" className="btn btn-info">
                  {items[8]}
                </Button>
              </Link>
            </div>
          </Col>

          <Col
            lg={3}
            xs={12}
            style={{
              padding: "0px",
            }}
          >
            <div className="barthelcards">
              {/* first card */}
              <div style={{ position: "relative" }}>
                <img
                  src={first}
                  style={{
                    width: "100%",
                  }}
                />
                <p className="cardwords">{items[5]}</p>
                <Link
                  to="/information"
                  style={{
                    position: "absolute",
                    left: "50%",
                    top: "80%",
                    transform: "translate(-50%,-50%)",
                    width: "100%",
                    textAlign: "center",
                  }}
                >
                  <Button
                    type="button"
                    className="btn btn-info"
                    onClick={() => colorDispatch({ type: "one" })}
                  >
                    {items[3]}
                  </Button>
                </Link>
              </div>
              {/* second card */}
              <div className="secondcard">
                <img
                  src={second}
                  style={{
                    width: "100%",
                  }}
                />
                <p className="cardwords">{items[6]}</p>
                <Link
                  to="/map"
                  style={{
                    position: "absolute",
                    left: "50%",
                    top: "80%",
                    transform: "translate(-50%,-50%)",
                    width: "100%",
                    textAlign: "center",
                  }}
                >
                  <Button
                    type="button"
                    className="btn btn-info"
                    onClick={() => colorDispatch({ type: "three" })}
                  >
                    {items[4]}
                  </Button>
                </Link>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </main>
  );
}

export default Barthel;
