import React, { useReducer, createContext } from "react";
import WebRouter from "./router/WebRouter";
import { HashRouter as Router } from "react-router-dom";
import "./static/css/common.less";
import Bottom from "./components/Bottom";
import ScrollToTop from "react-router-scroll-top";
import NavigationBar from "./components/Navigation/NavigationBar";

const initLang = "en";
const initialColor = [false, false, false, false, false, true];
export const LangContext = createContext(initLang);
export const EN = "en";
export const ZHCN = "zh-CN";
export const VI = "vi";
export const AR = "ar";
export const IT = "it";
export const ActiveContext = createContext(initialColor);

function colorReducer(color, action) {
  switch (action.type) {
    case "zero":
      return [true, false, false, false, false, false];
    case "one":
      return [false, true, false, false, false, false];
    case "two":
      return [false, false, true, false, false, false];
    case "three":
      return [false, false, false, true, false, false];
    case "four":
      return [false, false, false, false, true, false];
    case "five":
      return [false, false, false, false, false, true];
    default:
      return [false, false, false, false, false, true];
  }
}

const langReducer = (state, action) => {
  switch (action.type) {
    case EN:
      return "en";
    case AR:
      return "ar";
    case IT:
      return "it";
    case VI:
      return "vi";
    case ZHCN:
      return "zh-CN";
    default:
      return initLang;
  }
};

function App() {
  const [lang, langDispatch] = useReducer(langReducer, initLang);
  const [color, colorDispatch] = useReducer(colorReducer, initialColor);
  return (
    <ActiveContext.Provider value={{ color, colorDispatch }}>
      <LangContext.Provider value={{ lang, langDispatch }}>
        <Router routers={WebRouter}>
          <ScrollToTop>
            <NavigationBar />
            <WebRouter />
            <Bottom />
          </ScrollToTop>
        </Router>
      </LangContext.Provider>
    </ActiveContext.Provider>
  );
}

export default App;
