import React from "react";
import "./index.less";
import Convert from "../../../components/Translate/Translate";
import timeline from "../../../static/images/WechatIMG495.png";
function Spotting() {
  let language = localStorage.getItem("language");
  let text =
    "Spotting the Signs of Dementia|How to get a diagnosis of dementia? | Dementia affects each person in a different way, depending upon the impact of the disease and the " +
    "person’s personality before becoming ill. The signs and symptoms linked to dementia can be understood in three stages. | " +
    "Early stage| The early stage of dementia is often overlooked, because the onset is gradual. Common symptoms include:| " +
    "• Forgetfulness | • Losing track of the time | • Becoming lost in familiar places. | " +
    "Middle stage:| As dementia progresses to the middle stage, the signs and symptoms become clearer and more restricting. These include:| " +
    "• Becoming forgetful of recent events and people's names | • Becoming lost at home | • Having increasing difficulty with communication | " +
    "• Needing help with personal care | • Experiencing behaviour changes, including wandering and repeated questioning. | " +
    "Late stage:| The late stage of dementia is one of near total dependence and inactivity. Memory disturbances are serious, " +
    "and the physical signs and symptoms become more obvious. Symptoms include:| " +
    " • Becoming unaware of the time and place | • Having difficulty recognizing relatives and friends| • Having an increasing need for assisted self-care | " +
    " •	Having difficulty walking | • Experiencing behaviour changes that may escalate and include aggression. | " +
    "If someone is showing symptoms associated with dementia it is important to visit a GP as soon as possible. | " +
    "References:";
  let items, isloading;
  if (language !== "en") {
    let { convertedText, loading } = Convert(text, language);
    items = convertedText
      .split("|")
      .map((data) => (data = data.replace("&#39;", "'")));
    isloading = loading;
  } else {
    items = text.split("|");
    isloading = false;
  }

  let enVideos = ["https://www.youtube.com/embed/WgC3D-eG7EI"];
  let cnVideos = ["https://www.youtube.com/embed/Z9nyMvnXDAI"];
  let viVideos = ["https://www.youtube.com/embed/HgZDmKljPUM"];
  let arVideos = ["https://www.youtube.com/embed/dpVR_DlQkFg"];
  let itVideos = ["https://www.youtube.com/embed/si3qswabYxI"];
  let videos;
  switch (language) {
    case "zh-CN":
      videos = cnVideos;
      break;
    case "vi":
      videos = viVideos;
      break;
    case "ar":
      videos = arVideos;
      break;
    case "it":
      videos = itVideos;
      break;
    default:
      videos = enVideos;
  }

  return (
    <div className="diagnosing">
      {isloading ? (
        <div>...loading</div>
      ) : (
        <div>
          <h2>{items[0]}</h2>
          <div className="link-bottom "></div>
          <h5>{items[1]}</h5>
          <div className="container">
            <iframe
              title="videos3"
              src={videos[0]}
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
              className="video"
            ></iframe>
          </div>
          <p>{items[2]}</p>
          <img
            src={timeline}
            alt="timeline of stages"
            style={{ width: "100%" }}
          />

          <p>{items[22]}</p>
          <h5>{items[23]}</h5>
          <p className="ref">
            https://www.dementiauk.org/wp-content/uploads/2018/07/What-is-dementia-WEB-June-2018.pdf
          </p>
          <p className="ref">
            https://www.ageuk.org.uk/globalassets/age-scotland/documents/ia---factsheets/dementia/dem-8---what-is-dementia.pdf
          </p>
        </div>
      )}
    </div>
  );
}

export default Spotting;
