function SimpleMap() {
  const { agedCare, clinics, center, zoom } = useContext(Detail);
  let lang = localStorage.getItem("language");
  let text =
    "Address|Contact name|Phone|Email|Bulking billing|Frequency|Referral Templete|Roving service|Clinic Type|Telehealth|" +
    "Postcode|Beds|Description| Dementia Clinic in AU | Aged Care Service in VIC|Click icons on the map for details ";
  let items;
  if (lang !== "en") {
    let { convertedText } = Convert(text, lang);
    items = convertedText
      .split("|")
      .map((data) => (data = data.replace("&#39;", "'")));
  } else {
    items = text.split("|");
  }
  let location;
  if (clinics) {
    location = clinics.map((clinic) => (
      <AnyReactComponent
        key={clinic.id}
        lat={clinic.Latitude}
        lng={clinic.Longitude}
        name={clinic.name_memory_clinic}
        address={clinic.address}
        contact={clinic.contact_name_referrals}
        phone={clinic.phone_referrals}
        email={clinic.email_referrals}
        bulk={clinic.bulk_billing}
        frequency={clinic.frequency}
        referral={clinic.referral_template}
        roving={clinic.roving_services}
        type={clinic.location_type}
        telehealth={clinic.telehealth_services}
        postcode={clinic.postcode}
        structure={items}
        center={center}
      />
    ));
  }
  let agedcare;
  if (agedCare) {
    agedcare = agedCare.map((aged) => (
      <AnyReactComponent
        key={aged.series}
        lat={aged.latitude}
        lng={aged.longitude}
        address={aged.Address}
        beds={aged.Beds}
        description={aged.Description}
        name={aged.Name}
        phone={aged["Phone number"]}
        postcode={aged.Postcode}
        location={aged.Location}
        structure={items}
        center={center}
      />
    ));
  }
  return (
    // Important! Always set the container height explicitly
    <div style={{ height: "80vh", width: "100%" }}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyBdDbvl9MB85OUVGdKb4-IN_ayxScHalEs" }}
        center={center}
        zoom={zoom}
      >
        {agedcare}
        {location}
        <Container
          style={{
            position: "absolute",
            top: "-40vh",
            left: "-40vw",
          }}
        >
          <h5
            style={{
              padding: "10px 10px",
              display: "inline-block",
              background: "rgba(0,0,0,0.6)",
              color: "white",
            }}
          >
            {items[15]}
          </h5>
        </Container>
      </GoogleMapReact>
    </div>
  );
}

export default SimpleMap;
