import React from "react";
import "./index.less";
import Convert from "../../../components/Translate/Translate";
function Cultural() {
  let language = localStorage.getItem("language");
  let text =
    "Cultural Difference| Attending small details can make a big difference in meeting a person’s cultural needs. Culture influences how people define and respond to illness and explains variations in illness-related behaviours. If a person with dementia has very little or no spoken English, it will be essential to think of various ways to support them." +
    " | In Japan, for example, to age well is not only to avoid contracting diseases but also to maintain a circle of family and friends right up to the moment when we breathe our last. Being of sound mind and body means exerting ourselves mentally and physically, remaining deeply invested in our relationships, and receiving help from others. So long as we continue to enrich others’ lives, we can stay whole in ways that exceed the mere absence of a medical diagnosis." +
    " | A large segment of traditional Chinese culture tends to see such matters similarly. Confucianism places a premium on family, and the decline of cognitive capacities of seniors are not as the onset of a disease but an opportunity for friends and family to express how much they care. Assuming increasing responsibility for an aging loved one represents an opportunity to show how healthy the family is. The Hindu Indian Culture of India also prizes the opportunity to care for parents." +
    " | Americans tend to regard as a lamentable medical condition as part of life’s natural cycle and the passage into a second childhood. The emphasis is not on the stigma of dementia but rather on a withdrawal from worldly affairs to focus on other more essential matters. When an older person begins to show such signs, it is time for a natural transfer of authority to younger family members." +
    " | A UK-based study was comparing the number of people from White, Black and Asian ethnic groups diagnosed that people from the White ethnic group having the highest proportion of diagnoses. A Norwegian study analysed the diagnoses of one and a half million people over the age of 50. The researchers found a significantly higher proportion of diagnoses of dementia in the Norwegian group. The researchers involved in these studies acknowledge that whilst a lower prevalence of dementia in the immigrant groups cannot be ruled out." +
    " |References:";
  let items, isloading;
  if (language !== "en") {
    let { convertedText, loading } = Convert(text, language);
    items = convertedText
      .split("|")
      .map((data) => (data = data.replace("&#39;", "'")));
    isloading = loading;
  } else {
    items = text.split("|");
    isloading = false;
  }

  let enVideos = ["https://www.youtube.com/embed/dEBfSie6nIE"];
  let cnVideos = ["https://www.youtube.com/embed/0dnCOk0kz_A"];
  let viVideos = ["https://www.youtube.com/embed/5zshoTlb29g"];
  let arVideos = ["https://www.youtube.com/embed/9HXSrO61d4Y"];
  let itVideos = ["https://www.youtube.com/embed/TWcjKnnvi7E"];
  let videos;
  switch (language) {
    case "zh-CN":
      videos = cnVideos;
      break;
    case "vi":
      videos = viVideos;
      break;
    case "ar":
      videos = arVideos;
      break;
    case "it":
      videos = itVideos;
      break;
    default:
      videos = enVideos;
  }

  return (
    <div className="cultural">
      {isloading ? (
        <div>...loading</div>
      ) : (
        <div>
          <h2>{items[0]}</h2>
          <div className="link-bottom "></div>
          <p>{items[1]}</p>
          <div className="container">
            <iframe
              title="videos3"
              src={videos[0]}
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
              className="video"
            ></iframe>
          </div>
          <p>{items[2]}</p>
          <p>{items[3]}</p>
          <p>{items[4]}</p>
          <p>{items[5]}</p>
          <h5>{items[6]}</h5>
          <p className="ref">
            https://www.alzheimer-europe.org/content/download/170517/1288954
            /file/Final%20version%20of%20ethics%20report
            %20on%20minority%20ethnic%20groups %202018_47.pdf
          </p>
          <p className="ref">
            Mary R. Janevic, MPH, Cathleen M Connell, PhD, Racial, Ethnic, and
            Cultural Differences in the Dementia Caregiving Experience: Recent
            Findings, The Gerontologist, Volume 41, Issue 3, 1 June 2001, Pages
            334–347, https://doi.org/10.1093/geront/41.3.334
          </p>
          <p className="ref">
            https://www.cambridge.org/core/journals
            /international-psychogeriatrics/article/crosscultural-differences-in-dementia-the-sociocultural-health-belief-model
            /097E34C80CC79098F2945F701E89AAD6
          </p>
          <p className="ref">
            Cathleen M. Connell, PhD, Gloria D. Gibson, PhD, Racial, Ethnic, and
            Cultural Differences in Dementia Caregiving: Review and Analysis,
            The Gerontologist, Volume 37, Issue 3, June 1997, Pages 355–364,
            https://doi.org/10.1093/geront/37.3.355
          </p>
        </div>
      )}
    </div>
  );
}

export default Cultural;
