const useFetch = (url) => {
  const [homeitems, setHomeitems] = useState(null);
  useEffect(() => {
    (async () => {
      const response = await fetch(url);
      const data = await response.json();
      setHomeitems(data);
    })();
  }, [url]);
  return homeitems;
};
