import React from "react";
import { Route, Switch, HashRouter } from "react-router-dom";
import LanguagesPage from "../pages/LanguagesPage";
import HomePage from "../pages/HomePage";
import NotFound from "../pages/NotFound";
import InformationPage from "../pages/InformationPage";
import TestPage from "../pages/TestPage/TestPage";
import GPCOG from "../pages/TestPage/GPCOG/GPCOG";
import CIT from "../pages/TestPage/6CIT/CIT";
import MapPage from "../pages/MapPage";
import CheckPage from "../pages/CheckPage";
import Barthel from "../pages/CheckPage/Barthel";
import HomeSafety from "../pages/CheckPage/Home";
import Check from "../pages/CheckPage/Home/Check";
import Driving from "../pages/CheckPage/Driving";
import DrivingCheckA from "../pages/CheckPage/Driving/CheckA";
import DrivingCheckB from "../pages/CheckPage/Driving/CheckB";
import Lost from "../pages/CheckPage/Lost";
import Lostchecka from "../pages/CheckPage/Lost/Checka";
import Lostcheckb from "../pages/CheckPage/Lost/Checkb";
function WebRouter() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={LanguagesPage}></Route>
        <Route exact path="/home" component={HomePage}></Route>
        <Route exact path="/information" component={InformationPage}></Route>
        <Route exact path="/test" component={TestPage}></Route>
        <Route exact path="/test/gpcog" component={GPCOG}></Route>
        <Route exact path="/test/cit" component={CIT}></Route>
        <Route exact path="/check" component={CheckPage}></Route>
        <Route exact path="/check/barthel" component={Barthel}></Route>
        <Route exact path="/check/homesafety" component={HomeSafety}></Route>
        <Route exact path="/check/homesafety/check" component={Check}></Route>
        <Route exact path="/check/driving" component={Driving}></Route>
        <Route
          exact
          path="/check/driving/checka"
          component={DrivingCheckA}
        ></Route>
        <Route
          exact
          path="/check/driving/checkb"
          component={DrivingCheckB}
        ></Route>
        <Route exact path="/check/lost" component={Lost}></Route>
        <Route
          exact
          path="/check/lost/checka"
          component={Lostchecka}
        ></Route><Route
        exact
        path="/check/lost/checkb"
        component={Lostcheckb}
      ></Route>
        <Route exact path="/map" component={MapPage}></Route>
        <Route exact path="*" component={NotFound}></Route>
      </Switch>
    </HashRouter>
  );
}

export default WebRouter;
