/**
 * The first page when a user open the website.
 *
 * @author (Yechao Mao)
 * @version (26/10/2020)
 */
import React, { useContext } from "react";
import "./index.less";
import { Link } from "react-router-dom";
import australia from "../../static/images/australia.png";
import china from "../../static/images/china.png";
import italy from "../../static/images/italy.png";
import vietnam from "../../static/images/vietnam.png";
import arab from "../../static/images/united-arab-emirates.png";
import { LangContext, ZHCN, AR, IT, VI, EN, ActiveContext } from "../../App";

function Languages() {
  const { langDispatch } = useContext(LangContext);
  const { colorDispatch } = useContext(ActiveContext);
  return (
    <div className="languagepage">
      <main className="languages">
        <h2 style={{ margin: "0 auto", textAlign: "center" }}>
          KEEPING CALD DEMENTIA SAFE
        </h2>
        <p className="introwords">
          We are here to help you with CALD (Culturally and Linguistically
          Diverse) background who may have the language barrier to have a broad
          understanding of how to keep a dementia patient safe.
        </p>
        <h5>Please select a language</h5>
        <div className="firstrow">
          <Link
            to="/home"
            onClick={() => {
              langDispatch({ type: ZHCN });
              colorDispatch({ type: "zero" });
              localStorage.setItem("language", ZHCN);
            }}
          >
            <div className="card">
              <img src={china} className="card-img-top" alt="china" />
              <p>中文(由于翻译服务到期，暂时不可用)</p>
            </div>
          </Link>
          <Link
            to="/home"
            onClick={() => {
              colorDispatch({ type: "zero" });
              langDispatch({ type: EN });
              localStorage.setItem("language", EN);
            }}
          >
            <div className="card">
              <img src={australia} className="card-img-top" alt="australia" />
              <p>English</p>
            </div>
          </Link>
          <Link
            to="/home"
            onClick={() => {
              colorDispatch({ type: "zero" });
              langDispatch({ type: VI });
              localStorage.setItem("language", VI);
            }}
          >
            <div className="card">
              <img src={vietnam} className="card-img-top" alt="vietnam" />
              <p>Vietnamese(Do dịch vụ dịch hết hạn nên tạm thời không có)</p>
            </div>
          </Link>
        </div>
        <div className="secondrow">
          <Link
            to="/home"
            onClick={() => {
              colorDispatch({ type: "zero" });
              langDispatch({ type: AR });
              localStorage.setItem("language", AR);
            }}
          >
            <div className="card">
              <img src={arab} className="card-img-top" alt="arab" />
              <p>
                عربى(بسبب انتهاء صلاحية خدمة الترجمة ، فهي غير متوفرة مؤقتًا)
              </p>
            </div>
          </Link>
          <Link
            to={{ pathname: "/home", message: { language: "it" } }}
            onClick={() => {
              colorDispatch({ type: "zero" });
              langDispatch({ type: IT });
              localStorage.setItem("language", IT);
            }}
          >
            <div className="card">
              <img src={italy} className="card-img-top" alt="italy" />
              <p>
                italiano(A causa della scadenza del servizio di traduzione, è
                temporaneamente non disponibile)
              </p>
            </div>
          </Link>
        </div>
      </main>
    </div>
  );
}

export default Languages;
